
/**
 * Main Website Header
 */
.site-header {

    padding: 0 0.15em;
    background-color: transparent;
    position: absolute;
    left: 0;
    right: 0;
    z-index: 1001;
    display: flex;
    justify-content: space-between;
    transition: all .25s ease-in;

    @media #{$tablet__breakpoint} {

        position: absolute;

    }

}

.header-sticky {

    position: fixed;
    background-color: $hero-color-transparent;
    animation: fadeIn .5s ease-in;
    box-shadow: 0 4px 5px rgba(0, 0, 0, 0.2);

    a {

        color: $color-light;

    }

    .site-branding .custom-logo-link {
        max-width: 2.5em;
    }

    .site-title {

        a:link,
        a:visited {

        color: $color-light;

      }

    }

}

.site-branding,
.site-navigation {

    flex: 1 0 auto;

}

/**
  * (flex) center vertically header/branding content
  */
.custom-logo-link,
.site-branding-text-container,
.main-navigation {

    display: flex;
    flex-direction: column;
    justify-content: center;

    // have flexible height container: what is the em equivalent of 65px, 90px?
}

.site-branding {

    min-height: 65px;
    display: flex;
    margin-left: 3px;

    @media #{$tablet__breakpoint} {

        flex: 1 0 auto;

    }

    @media #{$desktop__breakpoint} {

        flex-basis: 45%;

    }

    .custom-logo-link {

        margin-right: 0.25em;
        min-width: 60px;
        max-width: 1.5em;
        /* NOTE: revisit max-width property of Logo */

        @media #{$tablet__breakpoint} {

            max-width: 2em;

        }

        @media #{$desktop__breakpoint} {

            max-width: 3em;

        }

        img {

            vertical-align: middle;
            height: 100%;

        }

    }

}

.site-title,
.site-description {

    margin: 0;

}

.site-title {

  display: none;

  a {

    color: $hero-color;
    text-decoration: none;
    white-space: nowrap;

    &:visited {

      color: $hero-color;

    }

  }

  @media #{$tablet__breakpoint} {

      display: block;

   }

}

.site-description {

    font-style: italic;
    display: none; // write function to toggle this off?? customizer??

}

.site-header:before,
.site-header:after {

    content: none; /* this removes clears around site header so that flexbox justify properties will work */

}
