.widget {

    width: 100%;
    font-family: $titillium_web;

    ul,
    ol {
        padding: .7em 0 0;
        margin-bottom: 0;
        //list-style-type: none;
        padding-top: 0;
        margin-top: 0;

            li {
                display: block;
                line-height: 2em;

                ul,
                ol {

                    margin-left: .5em;

                    @media screen and (min-width: 30em) {
                            margin-left: 1em;
                    }

                }

            }
    }

    /* Make sure select elements fit in widgets. */
    select {
        width: 100%;
        height: 2.5em;
        padding: 10px;
        //font-family: $titillium_web;
        border: 1px solid #ccc;
    }

    &:last-of-type {
        margin-bottom: 1em;
    }

    .widget-title {

        margin: ($base_scale + rem) 0 1rem;
        color: $color-light;

    }

}

.widget.widget_pages,
.widget.widget_nav_menu {

}

.blog {

  :not(.footer-widgets).widget-area {

    .widget-title {

      color: $hero-color;

    }

		padding: 0 1em;

    .widget_recent_entries {

      ul > li {
        display: list-item;
        list-style: disc inside;
        line-height: $second_line_height;
        margin-bottom: 0.75em;
      }

    }

    .social-media-menu {

  		  margin-left: 0;
        padding-top: 0;

        li {

          display: inline-block;
          line-height: 1;
          font-size: 200%;
          margin-right: 0;
          padding: 0;

          a {

            border-width: 0;
            padding: 0 0.25em;
            line-height: normal;

          }

      }

    }

  }

}

/* MailChimp signup form */
.widget #mc_embed_signup {

    p {

        margin: 0.5em 0 1em;

    }

    .indicates-required {

        display: none;

    }

    .mc-field-group {

        clear:left;
        position:relative;
        width:100%;
        padding-bottom: 1rem;
        min-height:50px;
        .asterisk {
            color: red;
        }

    }

    .mc-field-group label {
        display: block;
        font-weight: bold;
        font-size: 85%;
        margin-bottom: 0.25em;
    }
    .mc-field-group input {
        display: block;
        width: 100%;
        padding: 8px 0;
        text-indent: 2%;

    }
    #mce-responses .response {
        margin-bottom: .75em;
    }

    @media #{$desktop__breakpoint} {
        .mc-field-group input,
        div.mce_inline_error {
            width: 96%;
        }

    }

}

.widget_pages,
.widget_nav_menu {

	a {

    display: block;
		text-decoration: none;
		border-bottom: 1px dashed $color-light;

		&:hover,
		&:focus {
			// TODO: Investigate this style
			text-decoration: none;
			border-bottom: 1px dashed $color-dark;

		}

	}

}

/**
 * Show list items on 'Recent Posts' and other specific list-type footer widgets
 */
.widget.widget_recent_entries {

		ul > li {

			display: list-item;
			list-style: disc inside;
			line-height: 1.5;
			margin-bottom: 0.35em;

		}

}

