#maintenance-steps {
    h2 + p {
        /*font-weight: bold;*/
    }
}
#maintenance-how-works {
    //@include border;
    text-align: center;
    display: flex;
    flex-wrap: wrap;
    margin: 2em 0;
    .step-container {
        box-shadow: -1px -1px 8px 1px rgba(0,0,0,0.25);
        display: block;
        width: 100%;
        background-color: white;
        margin-bottom: 1em;
        padding: 1em;
        &:last-child {
            //@include border(2px, orange);
            margin-bottom: 0;
        }
        >img {
            display: block;
            margin: auto;
            //@include border;
            margin-bottom: 1em;
            height: 6em;
        }
        >p {
            //font-weight: bold;
            font-size: 90%;
        }
    }

    @media #{$tablet__breakpoint} {
        flex-wrap: nowrap;
        .step-container {
            margin: 0 0.5em;
        }
    }

}
